import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
// import {FlashMessagesService} from 'ng-flash-messages';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name:String;
  username:String;
  email:String;
  password:String;

  constructor(private validateService:ValidateService , private authService:AuthService , private router:Router) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    const user = {
      name:this.name,
      username:this.username,
      email:this.email,
      password:this.password
    }

    //Required all feilds
    if(!this.validateService.validateRegister(user)){
  //   this.flashMessage.showFlashMessage({
  //   // Array of messages each will be displayed in new line
  //   messages: ["Please fill all the enteries!"],
  //   // Whether the flash can be dismissed by the user defaults to false
  //   dismissible: true,
  //   // Time after which the flash disappears defaults to 2000ms
  //   timeout: false,
  //   // Type of flash message, it defaults to info and success, warning, danger types can also be used
  //   type: 'danger'
  // });
      return false;
    }

    if(!this.validateService.validateEmail(user.email)){
    //   this.flashMessage.showFlashMessage({
    //   // Array of messages each will be displayed in new line
    //   messages: ["Please Enter valid email!"],
    //   // Whether the flash can be dismissed by the user defaults to false
    //   dismissible: true,
    //   // Time after which the flash disappears defaults to 2000ms
    //   timeout: false,
    //   // Type of flash message, it defaults to info and success, warning, danger types can also be used
    //   type: 'danger'
    // });
      return false;
    }
    this.authService.registerUser(user).subscribe(data =>{
      if(data.success){
        console.log("Registered Successfully");
        this.router.navigate(['/login']);
      }else{
        console.log("Something Went Wrong");
        this.router.navigate(['/register']);
      }
    })
  }
}



