import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';
import { Subscription } from 'rxjs/Subscription';


@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  imageUploadLink:String;
  description:String;
  title:String;
  communityValue:String;
  community:Boolean;
  constructor(private authService:AuthService,private router:Router) { }

  ngOnInit() {
  }
  upload(event){
    this.imageUploadLink = event.fpfile.url;
  }

  onUpload(){
    const user = localStorage.getItem('user');
    const userDetails = JSON.parse(user);
    if(this.community){
      this.communityValue = "yes";
    }else{
      this.communityValue = "no";
    }

    const newImage = {
      userId : userDetails.userId,
      imageUrl : this.imageUploadLink,
      imageDesc : this.description,
      imageTitle:this.title,
      community:this.communityValue
    };
    console.log(newImage);

    this.authService.uploadImage(newImage).subscribe(data =>{
      if(data.success){
        console.log("Image Uploaded Successfully");
        this.router.navigate(['/dashboard']);
      }else{
        console.log('Something went wrong!!');
        this.router.navigate(['/uploadImage']);
      }
    })

  }
}



